.weather{
    margin: 10px;
    border-radius: 25px;
    height: auto;
    color: white;
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: flex-end;
    position: relative;
    min-width: 40%;
}
.weaPic{
    align-self: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: auto;
}
#weaIcon{
  height: 3.5em;
  margin: auto;
}
#wea{
    background-color: rgba(0,0,0,0);
    border: none;
    color: white;
    padding: 0;
    margin: 0;
    letter-spacing: normal;
    font-weight: 300;
    font-size: 0.8em;
}
#notice{
  position: absolute;
  bottom: 2px;
  color: rgba(255, 255, 255, 0.5);
}
p{
  padding: 0;
  margin: 0;
  font-size: 0.8em;
}
h1{
  margin: 0;
  margin-right: 5px;
  font-size: 3em;
  vertical-align: text-bottom;
}
#locationMarker{
  max-height: 0.7em;
  height: 0.7em;
}
.sunny{
    background: rgb(50,148,228);
    background: linear-gradient(120deg, rgba(50,148,228,1) 0%, rgba(144,184,254,1) 100%);
}
.dark{
    background: rgb(79,61,166);
    background: linear-gradient(120deg, rgba(79,61,166,1) 0%, rgba(217,114,176,1) 100%);
}
.rainy{
    background: rgb(103,229,221);
    background: linear-gradient(120deg, rgba(103,229,221,1) 0%, rgba(219,246,230,1) 100%);
}

@media(max-width: 1600px){
  .weather{
      display: flex;
      flex-wrap: wrap;
      align-items: flex-end;
      justify-content: center;
  }
}
@media(max-width: 991px){
  .weather{
      width: 100%;
      height: auto;
      max-width: 100%;
      min-width: 100%;
      display: grid;
      grid-template-columns: auto 1fr auto;
      align-items: flex-end;
  }
}
